#main-nav {
  position: fixed;
  z-index: 990;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  transform: translateY(-165%);
  transition-property: transform;
  transition-duration: 0.5s;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4); }

#main-nav ul {
  padding-left: 0;
  padding-top: 15%;
  height: 100%; }

#main-nav li {
  list-style: none; }

#main-nav li a {
  font-weight: 600;
  color: #373a3c;
  text-transform: uppercase;
  display: block;
  padding: 0.75rem 0.9375rem;
  text-align: center;
  width: 90%;
  border-bottom: 2px solid #373a3c;
  margin: 0 auto; }

#main-nav li a:hover {
  text-decoration: none;
  color: #972daa;
  border-color: #972daa; }

#main-nav.is-visible {
  transform: translateY(0); }

#main-nav ul.down {
  padding-top: 0; }

#main-nav ul.down a {
  color: #55595c;
  border-bottom: 1px solid #55595c;
  font-weight: 400;
  font-size: 0.9375rem; }

.close-menu {
  position: absolute;
  top: 0.625rem;
  left: 50%;
  margin-left: -1.25rem;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  overflow: hidden; }

.close-menu:before,
.close-menu:after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 1.125rem;
  left: 0.375rem;
  width: 1.75rem;
  height: 0.375rem;
  background-color: #732282;
  transition-property: transform;
  transition-duration: 0.3s; }

.close-menu:before {
  transform: rotate(45deg); }

.close-menu:after {
  transform: rotate(135deg); }

@media (min-width: 48em) {
  #main-nav ul {
    padding-top: 10%; }
  #main-nav li a {
    font-size: 2rem;
    width: 50%; }
  #main-nav ul.down a {
    font-size: 1.3125rem; }
  .close-menu {
    top: 2.125rem; } }

@media (min-width: 62em) {
  #main-nav ul {
